body {
  margin: 0;

  background-color: #f7f7f7;
  font-family: "Rubik", sans-serif;
}

#BodyContainer {
  display: flex;
}
#MainContainer {
  padding: 8px;
  border: solid 1px rgb(207, 203, 202);
  border-radius: 10px;
  margin-bottom: 10px;
}
/* Nav ##########################*/
nav {
  position: sticky;
  top: 0;
  width: 100%;
  height: 64px;
  background-color: rgb(95, 170, 82);
  text-align: center;
  color: white;
  z-index: 2;
}
.CompanyTitle {
  float: left;
  margin-left: 17px;
  cursor: pointer;
  margin-top: 13px;
  margin-bottom: 0;
  font-size: x-large;
  font-family: cursive;
  font-weight: bolder;
  color: white;
}
/* .CompanyTitleLink{
  
} */

/* a {
  color:orange;
}
a:hover {
   color: gold;
} */

nav h6 {
  float: right;
  margin-right: 20px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 26px;
}
nav input {
  margin-top: 16px;
  padding: 8px;
  border: none;
  border-radius: 8px;
  width: 70px;
  cursor: pointer;
  transition: all 0.35s ease-in-out;
  float: left;
  margin-left: 50px;

  background-image: url(/assest/NavIcon/search.png);
  background-position: 5px 5px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  padding-left: 30px;
}
#searchIcon {
  position: absolute;
  right: 80px;
  fill: white;
  margin: 0;
  top: 50%;
  transform: translate(0, -50%);
  z-index: -1;
  cursor: pointer;
  display: none;
}
nav input:hover {
  width: 150px;
}
@media only screen and (max-width: 450px) {
  nav input {
    display: none;
    position: fixed;
    top: 54px;
    left: 50%;
    transform: translate(-57%, 0);
    margin: 20px;
    width: 70%;
    opacity: 0;
  }
  nav input:hover {
    width: 70%;
  }
  #searchIcon {
    display: block;
  }
}

#basket {
  position: absolute;
  right: 20px;
  fill: white;
  margin: 0;
  top: 50%;
  transform: translate(0, -50%);
  z-index: -1;
  cursor: pointer;
}
::placeholder {
  font-style: italic;
}

/*ads####################################*/
#ads {
  width: 100%;
  height: 50px;
  /* background-color: rgb(68, 102, 117);
   */
  background-color: rgba(56, 125, 122, 1);
}
.adsTextElement {
  position: relative;
  display: flow-root;
  color: white;
  margin: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

/* DesktopMenu ------------------------------------------------*/

#HeaderView {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
#DesktopMenuTitel {
  text-align: center;
  /* border-radius: 10px; */
  background-color: #f7f7f7;
  padding: 10px;
  margin: 2px;
  margin-bottom: 4px;
}
#DesktopMenuContainer {
  margin-left: 8px;
  margin-right: 10px;
  padding: 0px;
  overflow: hidden;
  border-radius: 10px;
  display: inline-block;
  /* background-color: rgba(255, 250, 234, 0.513); */
  background-color: white;
  position: relative;
  box-sizing: border-box;
  transition: all 0.5s;
  border: solid 1px rgb(207, 203, 202);
}

#DesktopMenuList {
  list-style-type: none;
  margin-top: 7px;
  padding: 0;
  line-height: 39px;
  width: 183px;
  /* columns: 2; */
  /* -webkit-columns: 2;
  -moz-columns: 2; */
  user-select: none;
}

.DesktopMenuListItem {
  border-radius: 10px;
  /* background-color: #f7f7f7; */
  margin: 5px;
  text-align: center;
  border: solid 1px #f7f7f7;
  box-sizing: border-box;
  transition: all 0.5s;
  cursor: pointer;
  position: relative;
}
.DesktopMenuListItem:hover {
  border: solid 1px white;
  /* box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.3); */
  transform: scale(1.01);
  background-color: white;
  font-size: large;
}
.MenuSubMenu {
  list-style-type: none;
  text-align: center;
  font-size: small;
  margin-left: -45px;
  background-color: #f7f7f7;
  display: none;
  transform: scaleY(0);
  transform-origin: top;
  transition: all 0.35s;
}
.collaps {
  position: absolute;
  left: 5px;
  top: 9px;
  transform: rotate(-90deg);
  transition: all 0.35s;
}
.collaps:hover {
  fill: red;
}
/* slider ############### */
#sliderContainer {
  /* float: right; */
  width: 99%;
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  user-select: none;
  /* background-color:rgba(2, 195, 154, 1); */
  margin: 0px;
}
.sliderElement {
  width: 100%;
  height: 250px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 95%;
  /* border-radius: 62px; */
  display: inline-block;
  transition: all 1s ease-in-out;
  padding: 0;
  /* background-color: white; */
  /* background-color:rgba(2, 195, 154, 1); */
  background-color: #f7f7f7;
  margin: auto;
  /* margin-bottom: 5%; */
  margin-top: 5px;
}

#sliderContainer:hover #leftArrow {
  opacity: 1;
}
#sliderContainer:hover #rightArrow {
  opacity: 1;
}
.arrow {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  opacity: 0;
  fill: white;
  stroke: green;
  stroke-width: 0.4px;
  cursor: pointer;
  transition: all 0.5s;
}
#leftArrow {
  left: 25px;
}
#rightArrow {
  right: 25px;
}
#dots {
  opacity: 0;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translate(-50%);
  transition: all 0.5s;
}
#dots svg {
  fill: green;
  cursor: pointer;
}
#sliderContainer:hover #dots {
  opacity: 1;
}

/* EmpfehlungenContainer */
.EmpfehlungenContainer {
  position: relative;
  width: calc(100vw - 240px);
  stroke-width: 1px;
  white-space: nowrap;
  overflow: hidden;
  margin-bottom: 13px;
  padding-bottom: 1px;
}
.fullWidthProductWindows {
  width: 100%;
}

.EmpfehlungenContainer:hover .productArrow {
  opacity: 1;
}
.productArrow {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  border-radius: 50%;
  background-color: #f7f7f7;
  fill: rgb(149, 146, 146);
  opacity: 0;
  z-index: 1;
  transition: opacity 0.5s;
}
.productLeftArrow {
  left: 0;
}
.productRightArrow {
  right: 0;
}

.EmpfehlungenContainerTitle {
  margin-top: 3px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 5px;
  font-family: "Rubik", sans-serif;

  /* background-color:rgba(255, 120, 90, 1); */
  /* background-color: rgba(254, 216, 102, 0.322);  */
  /* background-color: rgba(56, 125, 122, 1);; */
  /* background-color: #5d5d5d5d; */

  padding: 12px;
  border-radius: 10px;
  font-size: large;
  text-align: left;
}

@media only screen and (max-width: 600px) {
  #asideContainer {
    display: none;
  }
  .EmpfehlungenContainer {
    width: 94vw;
  }
}

/*productContainer*/

.productContainer {
  width: 285px;
  height: 400px;
  background-color: #5d5d5d;
  margin: 10px;
  padding-bottom: 5px;
  border: solid 1px rgb(207, 203, 202);
  border-radius: 10px;
  padding: 20px;
  display: inline-block;
  /* background-color: rgba(255, 250, 234, 0.513); */
  background-color: white;
  position: relative;
  box-sizing: border-box;
  transition: all 0.5s;
}
.productContainer:hover {
  border: none;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  transform: scale(1.01);
}
.productContainer * {
  margin: 2px;
}

.productContainer h1 h2 h3 h4 h5 h6 {
  color: rgb(39, 36, 34);
  font-size: 16px;
  line-height: 24px;
  font-family: SourceSansPro, Arial, sans-serif;
  font-weight: 700;
  letter-spacing: normal;
}
.productPic {
  width: 100%;
  height: 220px;
  margin-bottom: 20px;
  background-size: cover;
}
.action h4 {
  margin: 2px;
  font-size: x-large;
  color: #f7f7f7;
}
.action {
  position: absolute;
  top: 1px;
  left: 1px;
  /* background-color: red; */
  border-radius: 10px;
  padding: 5px;
}
#productRating {
  margin-left: 10px;
}
#ratespan {
  margin-left: 10px;
  margin-top: 10px;
  font-size: 8px;
}
#productName {
  margin-left: 10px;
  font-size: large;
  overflow: hidden;
  height: 43px;
  word-break: break-all;
  white-space: initial;
  margin-bottom: 3px;
}

#productPrice {
  margin-left: 10px;
  font-size: large;
  color: red;
}
#productNote {
  margin-left: 10px;
  margin-top: 0;
  font-size: medium;
  margin-bottom: 10px;
  overflow: hidden;
}
.ProductPricing {
  white-space: nowrap;
  margin-left: 10px;
}
.ProductPricing h5 {
  display: inline;
}
#productOldPrice {
  text-decoration: line-through;
}

/* SideNavBar *************************************/
#SideNavBarContainer {
  position: fixed;
  left: 0;
  top: 0;
  width: 220px;
  height: 100vh;
  background-color: teal;
  transition: left 0.5s;
  touch-action: none;
  z-index: 5;
  display: none;
}
#bluredBG {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  touch-action: none;
  background-color: rgba(0, 0, 0, 0.9);
  display: none;
  opacity: 0;
  transition: all 0.5s;
  display: none;
}
#hamButton {
  float: left;
  margin-top: 18px;
  margin-left: 10px;
  fill: white;
  cursor: pointer;
  display: none;
}

@media only screen and (max-width: 600px) {
  #hamButton {
    display: block;
  }
  #SideNavBarContainer {
    display: block;
  }
  /* #bluredBG {
    display: block;
  } */
  #DesktopMenuTitel {
    background: initial;
    font: inherit;
    float: initial;
    text-align: center;
  }
  .DesktopMenuListItem {
    background-color: initial;
  }

  .DesktopMenuListItem:hover {
    background-color: initial;
  }
  .MenuSubMenu {
    background-color: initial;
  }
  #DesktopMenuList {
    margin: auto;
  }

  .collaps {
    fill: white;
  }
}
#sideNavBarClose {
  position: absolute;
  top: 10px;
  right: 10px;
  fill: white;
  cursor: pointer;
}
#sideNavBarContent {
  user-select: none;
  width: 100%;
  height: 90vh;
  margin-top: 10vh;
}
#sideNavBarContent h5 {
  text-align: left;
  margin: 20px;
  text-align-last: justify;
  cursor: pointer;
  user-select: none;
}

